import React from "react";
import ReactDOM from "react-dom";
import "./index.css";

const TweetQuote = (props) => {
  return (
    <a id="tweet-quote" style={{color: "#fff"}} href="https://www.twitter.com/intent/tweet" target="_blank">
      <i className = "fa fa-twitter"></i>
    </a>
  );
}

const NewQuote = (props) => {
  const style = {
    margin: "0 1rem",
    borderRadius: "5px",
    backgroundColor: "rgba(255, 255, 255, 1)",
    color: props.color,
    float: "right",
    cursor: "pointer"
  }
  return (
    <button className="hoverRight" style={style} id="new-quote" onClick={props.newQuote}>
      <i className="fa fa-chevron-right"></i>
    </button>
  );
}

const Author = (props) => {
  return (
    <address id="author">
      --{props.author}
    </address>
  );
}

const Text = (props) => {
  return (
    <p id="text">
      {props.quote}
    </p>
  );
}

class QuoteBox extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      quoteOBJ : this.props.quotes[Math.floor(Math.random()*10)]
    }
  }

  newQuote = (event) => {
    this.setState((state, props) => {
      let r = Math.floor(Math.random()*10);
      const previousIndex = props.quotes.indexOf(state.quoteOBJ);
      while (r === previousIndex) {
        r = Math.floor(Math.random()*10);
      }
      return ({
          quoteOBJ: this.props.quotes[r]
        });
    })
  }


  render() {
    const quote = this.state.quoteOBJ["quote"];
    const author = this.state.quoteOBJ["author"];
    const color = this.state.quoteOBJ["color"];
    const quoteBoxStyle = {
      backgroundColor: color,
      color: "#fff",
      boxShadow: "-5px 5px black",
      borderRadius: "1rem",
      maxWidth: "500px",
      padding: "1.5em",
      margin: "10rem auto",
      fontSize: "1.5rem",
      height: "auto",
      transition: "background-color .5s ease-in-out, height 3s ease-in-out"
    }

    return (
      <div style={quoteBoxStyle} id="quote-box">
        <Text quote={quote}/>
        <Author style={{float:"right"}} author={author}/>
        <div style={{display:"flex",justifyContent:"flex-end"}}>
          <TweetQuote />
          <NewQuote color={color} newQuote = {this.newQuote}/>
        </div>
      </div>
    );
  }
}

